@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Montserrat", sans-serif}.App .logo{position:absolute;width:48px;height:48px;left:28px;top:28px}.Page{display:flex;flex-direction:column;margin-top:142px;margin-left:137px}

.TextField{margin-bottom:20px;display:flex;flex-direction:column;width:320px}.TextField input{width:320px;height:44px;left:141px;top:163px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:12px}.TextField input:focus,.TextField input:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.TextField>div{display:flex;justify-content:space-between;margin-bottom:5px}.TextField>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.TextField>div div:nth-child(2){font-size:12px;line-height:15px;font-weight:400;text-transform:uppercase;color:rgba(31,32,65,0.5)}

.DropDown{display:flex;flex-direction:column;width:320px}.DropDown .Visitors{display:flex;align-items:center;justify-content:space-between;width:320px;height:44px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:14px;padding-right:15px}.DropDown .Visitors:focus,.DropDown .Visitors:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.DropDown .Visitors img{-webkit-user-select:none;-ms-user-select:none;user-select:none}.DropDown>div{display:flex;justify-content:space-between;margin-bottom:4px}.DropDown>div div:nth-child(1){font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase}.DropDown>div div:nth-child(2){font-size:12px;line-height:15px;font-weight:400;text-transform:uppercase;color:rgba(31,32,65,0.5)}.DropDown .guest-list{height:159px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:10px;padding-right:10px;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#1f2041}.DropDown .guest-list .guest-list-adults{display:flex;justify-content:space-between;align-items:center}.DropDown .guest-list .guest-list-adults>div{display:flex;align-items:center}.DropDown .guest-list .guest-list-adults>div .minus,.DropDown .guest-list .guest-list-adults>div .plus{display:flex;align-items:center;justify-content:center;width:30px;height:30px;color:#1f2041;cursor:pointer;border:1px solid;border-color:#1f2041;opacity:0.25;border-radius:22px;margin:10px}.DropDown .guest-list .guest-list-adults>div .minus.clicked,.DropDown .guest-list .guest-list-adults>div .plus.clicked{color:#1f2041;border-color:#1f2041;opacity:0.5}

